export default {"name":"福鼎白茶","img":"\\baicha\\fudingbaicha\\img_20191122101122662ucf.jpg","desc":["福鼎白茶，福建省福鼎市特产，有道是：“一年茶三年药七年宝”。这“一年茶”说的是当年新白茶，茶汤清新淡然;“三年药”指的是存放了三四年的白茶，香气已渐趋醇和;“七年宝”。一般来说，白茶存放至五六年、六七年，就已算是老白茶了，也正是因为黄铜随着时间的推移，含量会逐年递增，此时的白茶显得极为珍贵。福鼎白茶是六大茶类中工艺最简单的一类茶，工艺：采摘—萎凋—干燥。，不经杀青或揉捻，只凭摊晾和干燥即可，若有太阳可在日光下晾晒。所以，白茶也有“日晒茶“之分。因其加工工序简单，历史自然最为久远，也最为难得。长期以来茶的好坏，在很大程度上是要看天公的脸色，好天出好茶。"],"menus":[{"name":"福鼎白茶茶叶历史","content":[{"text":"唐代陆羽著的《茶经》引用隋代的《永嘉图经》：“永嘉县东三百里有\n        白茶\n        山”。据陈椽、张天福等茶业专家考证，白茶山就是太姥山。说明早在隋唐，白茶就已出现。"},{"text":"2009年考古工作者在宋代吕氏家族墓的发掘中，发现了铜质渣斗里有30多枚极品白茶芽头，据专家推断，这些距今1000多年前的茶叶来源于福建福鼎。明代田艺蘅《煮泉小品》中记载：“茶者以火作者为次，生晒者为上，亦近自然，且断火气耳。况作人手器不洁，火候失宜，皆能损其香色也。生晒茶沦于瓯中，则旗枪舒畅，清翠鲜明，尤为可爱。”田艺蘅所述的，正是白毫银针的制法——生晒，制作禁忌不用手直接接触茶叶，制作的功力不足茶叶的香气就会受损，白毫银针泡在瓯中，条条银针直立，汤色鲜明。"},{"text":"明末清初周亮工莅临太姥山，为福鼎大白茶母茶树题诗：“太姥山高绿雪芽，洞天新泛海天槎。茗禅过岭全平等，义酒还教伴义茶。”现鸿雪洞中留有摩崖石刻。清代民国时期，白茶作为高端茶叶出口欧美，英国贵族阶层泡\n        红茶\n        时放入几根白毫银针，显示其珍贵。"},{"text":"1938年，点头镇龙田人李得光成立福鼎白茶合作社，茶农可直接向联社所辖的村社交茶、领款有民国时期李得光向茶农收购茶青的字据为证。国外的科研机构早就对白茶的保健功效进行研究。原全国政协委员、国家茶叶质量监督检测中心原主任骆少君研究员说：福鼎白茶是“墙内开花墙外香”一族。"},{"text":"原福建省茶叶协会秘书长陈金水说：晚清以来，北京同仁堂每年购50斤陈年白茶用以配药。在计划经济的时候，国家每年都要向福建省茶叶部门调拨白茶给国家医药总公司做药引(配伍)，配制成非常高级的药。进入21世纪，白茶得到英国凯特王妃的喜好，2009年英国王子世纪婚礼的结婚纪念茶就是用福鼎白茶配制而成。"},{"src":"\\baicha\\fudingbaicha\\img_201911221011225s3swt.jpg"}]},{"name":"福鼎白茶茶叶特点","content":[{"text":"福鼎\n        白茶\n        就是用产自福鼎“华茶1号”或“华茶2号”茶树的芽叶，不炒不揉，特殊工艺制作而成，其外形芽毫完整，汤色杏黄清澈，滋味清淡、清甜爽口的品质特点。根据采摘芽叶的不同，白茶可分为：白毫银针、白牡丹、寿眉、新工艺白茶等，近年来，根据市场需要又推出紧压白茶等。"},{"text":"功效"},{"text":"白茶的自由基含量最低，黄酮含量最高，氨基酸含量平均值高于其他茶类。而且福鼎白\n        茶具\n        有清热祛火的功效，白茶提取物对引起葡萄球菌感染、链球菌感染、肺炎等细菌生长具有预防作用。"},{"text":"茶树形态"},{"text":"福鼎大白茶树福鼎大白茶树高1.5—2米，幅宽1.6—2米，树势半开张，为小乔木型。分枝较密，节间尚长。树皮灰色。叶椭圆形，先端渐尖并略下垂，基部稍钝，叶缘略向上。通常大12×5.4厘米，长宽比平均为2.2。叶色黄绿、具光泽。侧脉明显，7—11对。锯齿较整齐、明显，27—38对。叶肉略厚，尚软。"},{"text":"一芽二叶长5.1厘米，百芽重23克。花型较大，雄蕊低于雌蕊，盛花期10月下旬至11月中旬，花量多，结果率高，茶子大而饱满。发芽期在3月上旬，11月中旬停止生长。生长期全年达8个月。"},{"text":"生长势旺盛，抗逆性强，耐旱亦耐寒，虽在零下3—4℃或更低亦不受冻。繁殖力强，压条、扦插发根容易，成活率高达95%以上。产量比当地菜茶高。制成\n        红茶\n        、\n        绿茶\n        、白茶品质均佳。采制银针以芽洁白肥壮、茸毛多最为特色。"},{"src":"\\baicha\\fudingbaicha\\img_20191122101122k0et80.jpg"}]},{"name":"福鼎白茶加工工艺","content":[{"text":"1、采摘"},{"text":"白茶\n        根据气温采摘玉白色一芽一叶初展鲜叶，做到早采、嫩采、勤采、净采。芽叶成朵，大小均匀，留柄要短。轻采轻放。竹篓盛装、竹筐贮运。"},{"text":"2、萎凋"},{"text":"采摘鲜叶用竹匾及时摊放，厚度均匀，不可翻动。摊青后，根据气候条件和鲜叶等级，灵活选用室内自然萎凋、复式萎凋或加温萎凋。当茶叶达七、八成干时，室内自然萎凋和复式萎凋都需进行并筛。"},{"text":"3、烘干"},{"text":"初烘：烘干机温度100-120℃，时间：10分钟;摊凉：15分钟。复烘：温度80-90℃;低温长烘70℃左右。"},{"src":"\\baicha\\fudingbaicha\\img_20191122101122fsckdv.jpg"}]},{"name":"福鼎白茶储藏方法","content":[{"text":"茶叶干茶含水分控制在5%以内，可常温保存，在常温、干燥、避光无异味条件下可以长期存放。"},{"src":"\\baicha\\fudingbaicha\\img_20191122101122yuacxp.jpg"}]},{"name":"福鼎白茶冲泡方法","content":[{"text":"福鼎\n        白茶\n        是中国传统六大茶类中最古老、最自然、最健康的白茶类茶叶，素为茶中珍品。白茶是保健功效最全面的一个茶类，具有抗辐射、抗氧化、抗肿瘤、降血压、降血脂、降血糖的功能。今天就和小编一起来了解下福鼎白茶的泡法。"},{"text":"白茶又分为白毫银针、白牡丹、寿眉和新工艺白茶等，亦称侨销茶，昔日，品白茶，是贵族身份的象征。"},{"text":"成茶多为芽头，满披白毫，色泽呈白色如银似雪而得名，故称“白茶”。白茶主要产区分布在福鼎、政和、建阳、松溪等地，是福建特有的茶类之一，由福鼎首创，进而传到水吉，最后传到政和。优质的白茶原产地太姥山高山环境、名优的国家级茶树良种(福鼎大白茶、福鼎大白毫茶树)、简单独特的萎凋和烘焙加工工艺造就了福鼎白茶外表茸毫密披、天然素雅、内质毫香幽远、清甜爽口的独特品质和出色白茶保健功效。近几年国人慢慢发现福鼎白茶的功效与作用，因此身为茶客的您了解福鼎白茶与\n        安吉白茶\n        的区别并熟悉白茶的泡法是您的茶叶必修课了。"},{"text":"福鼎白茶性寒凉功同犀角，对于胃“热”者可在空腹时适量饮用，胃中性者，随时饮用都无妨，而胃“寒”者则要在饭后饮用,但白茶一般情况下是不会刺激胃壁的，老年人饮白茶用于保健每天茶叶用量不宜太多，其他茶也是如此，肾虚体弱者、心率过快的心脏病人、严重高血压患者、严重便秘者、严重神经衰弱者、缺铁性贫血者都不宜喝浓茶，也不宜空腹喝茶，否则可能引起“茶醉”现象。"},{"text":"福鼎白茶的泡法如下："},{"text":"1、杯泡法：适合一人独饮，用杯泡法，用200ml透明玻璃杯，取3-5g用约90度开水，先洗茶温润闻香，再用开水直接冲泡白茶，冲泡时间根据个人口感自由掌握。"},{"text":"2、 盖碗法：适合二人对饮，取3g福鼎白茶投入盖碗，用90度开水洗茶温润闻香，然后象功夫茶泡法，第一泡30-45秒，以后每次递减，这样能品到福鼎白茶的清新口感。"},{"text":"3、壶泡法：适合三五人雅聚，大肚\n        紫砂壶\n        茶具\n        最佳或大容量飘逸杯，取5-6g福鼎白茶投入其中，用约90度开水洗茶温润闻香，45秒后即可品饮，特点：毫香醇厚。"},{"text":"4、大壶法：适合群体共饮和长时间饮用，取10-15g白茶投入大瓶瓷壶中，用90-100度开水直接冲泡，喝完蓄水，白茶具有耐泡，长时间搁置，口感依然淡雅醇香的特点，可从早喝到晚，适合家庭夏天消暑用茶。"},{"text":"5、煮饮法：适合保健用途，用清水投入10g陈3年以上陈年老白茶，煮至3分钟至浓汁滤出茶水，待凉70度添加大块冰糖或蜂蜜乘热饮用，常用于治疗嗓子发炎退烧 治疗水土不服，口感醇厚奇特。亦有夏天冰镇后饮用别有一番风味。"},{"text":"6、冷跑法：取袋泡白茶(品品香茶业冷泡白茶)，可置于矿泉水中，摇晃5分钟后即可饮用，风味俱佳，出门旅行运动时也可喝上白茶。"},{"text":"以上就是关于福鼎白茶的泡法的详细，大家冲泡白茶时候一定要放入适量的茶叶，饮用过多、过浓的茶，对人体是会造成伤害的。"}]},{"name":"福鼎白茶功效作用","content":[{"text":"白茶\n        比\n        红茶\n        、\n        绿茶\n        在制作方法上更为简单。一般来说，绿茶是没有发酵过的茶，制作中要经过采摘、炒青、揉捻、干燥等过程。白茶是把茶叶采摘下来后，只经过轻微的、约10%-30%的发酵，不经过任何炒青或揉捻动作，便直接晒干或烘干制作而成的。由于制作过程简单，以最少的工序进行加工，因此，白茶在很大程度上保留了茶叶中的营养成分。"},{"text":"专家指出，和绿茶、\n        乌龙茶\n        相比，白茶中茶多酚的含量较高，它是天然的抗氧化剂，可以起到提高免疫力和保护心血管等作用。白茶中还含有人体所必需的活性酶，可以促进脂肪分解代谢，有效控制胰岛素分泌量，分解体内血液中多余的糖分，促进血糖平衡。夏天经常喝白茶的人，很少中暑。专家认为，这是因为白茶中含有多种氨基酸，具有退热、祛暑、解毒的功效。"},{"text":"保健功效"},{"text":"①白\n        茶具\n        有清热祛火的功效，同时最新研究还表明，白茶提取物对导致葡萄球菌感染、链球菌感染、肺炎等细菌生长具有预防作用。"},{"text":"②白茶的自由基含量最低，多喝白茶或使用白茶的提取物，可以延缓衰老，美容美颜，因此受到了现代时尚人士，特别是都市女性的欢迎。"},{"text":"③喝红葡萄酒饮白茶，“一红一白”结合，白茶可以解决饮用红葡萄酒容易上火的难题，可以说是现代成功人士社交应酬的好伴侣。"},{"text":"④白茶可以清热降火，让人清心除烦、安神定智，有助于人们获得健康良好的睡眠。"},{"text":"⑤白茶具有耐泡的特点，一天旅途一杯茶，可以很好地缓解或消除旅行中的疲劳。"}]},{"name":"福鼎白茶历史故事","content":[{"text":"福鼎\n          白茶\n          原产于福鼎太姥山。据传说，太姥山古名才山，尧帝时有一老母在此居住，以种兰为业，为人乐善好施，深得人心，并曾将其所种绿雪芽茶作为治疗麻疹圣药，救活很多小孩，人们感恩戴德，把她奉为神明，称她为太母，这座山也因此名为太母山(后改为太姥山)。"}]},{"name":"福鼎白茶获奖情况","content":[{"text":"福鼎产\n          白茶\n          历史悠久，很早就有白茶贸易，史料记载(福鼎)白琳是地方的茶商聚集处。清康熙二十二年(1683年)，福鼎沙埕港设贸易口岸，出口茶叶。清嘉庆初年，“白毫银针”被誉为世界名茶，为英国女皇酷爱的珍品。清末民初，福鼎白茶已远销欧亚39个国家和地区。1935年，当代中国茶圣吴觉衣倡导在福鼎白琳建立茶叶初制厂。1936年，又在福鼎沙埕设立茶叶检验办事处，专事茶叶的进出口商检业务。"}]},{"name":"福鼎白茶鉴别好茶","content":[{"text":"感官特色：色泽墨绿或灰绿，毫显、色银白;口感甘醇、爽口;芽头肥壮、叶张肥嫩;毫香浓郁持久，并伴有花香;汤色杏黄明亮;叶底柔软明亮。\n        白茶\n        按原料及加工工艺的不同分白毫银针、白牡丹、新工艺白茶。"}]}]}